$color-blue: #0056A9;
$color-red: #C23349;
$color-green: #4CBB51;
$color-dark-blue: #1731B9;

.section {}

.first-section {
    min-height: 705px;
    background: $color-blue;
    &__content {
        max-width: 1140px;
        margin: 0 auto; }
    &__title {
        font-size: 55px;
        color: white;
        text-align: center;
        margin-bottom: 35px; } }

.second-section {
    min-height: 490px;
    background: $color-blue;
    &__cards-box {
        max-width: 1140px;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        margin-bottom: 60px; }
    &__hr {
        width: 506px; } }

.cards-item {
    position: relative;
    height: 360px;
    width: 360px;
    border-radius: 20px;
    margin-right: 30px;
    border: 2px solid #FFF;
    box-shadow: 8px 4px 4px 0px rgba(0, 0, 0, 0.25);
    &:last-child {
        margin-right: 0; } }

.cards-item_red {
    background: $color-red; }

.cards-item_green {
    background: $color-green; }

.cards-item_blue {
    background: $color-dark-blue; }





